import { createRouter, createWebHashHistory } from 'vue-router'
import Login from '../views/Login.vue';
import Semanas from '../views/Semanas.vue';
import Nuevasabana from '../views/Nuevasabana.vue';

const routes = [
  {
    path: "/:catchAll(.*)",
    name: 'Nuevasabana*',
    component: Nuevasabana
  },

  {
    path: '/crear',
    name: 'Nuevasabana',
    component: Nuevasabana
  },

  {
    path: '/login',
    name: 'Login',
    component: Login
  },

  {
    path: '/semanas/:idDestino',
    name: 'Semanas',
    component: Semanas
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
